

body {
            
  padding: 0;
  background: linear-gradient(90deg, rgba(135,206,250,1) 0%, rgba(144,238,144,1) 100%);
  color: #fff;
  margin: 0;
  font-family: "Nunito", sans-serif;
}

header {
  margin-top: 1vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 8vw;
  position: relative;
  z-index: 100;
}

header h2{
  color: whitesmoke;
}

.logo {
  display: flex;
  align-items: center;
  color: white;
  font-size: 1.5vw;
  font-weight: bold;
}

.logo img {
  height: 12vw;
  margin-right: 1vw;
  margin-left: -2vw;
  border-radius: 50%;
  position: relative;
  transition: transform 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.logo img:hover {
  transform: scale3d(0.9, 0.9, 0.9);
}

nav {
  display: flex;
  gap: 4vw;
}

nav a {
  color: white;
  text-decoration: none;
  font-size: 1.5vw;
  position: relative;
}

nav a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0.2vw;
  display: block;
  background: white;
  transition: width .3s;
  bottom: -0.5vw;
  left: 0;
}

nav a:hover::after {
  width: 100%;
}

.container{
  margin-left: 10vw;
  margin-right: 10vw;
  margin-bottom: 2vw;
  align-items: center;
}

.checkout-section {
  backdrop-filter : blur(1vw);
  color: #333;
  align-items: center;
  padding: 2vw;
  border-radius: 1vw;
  box-shadow: 0 0.4vw 1vw rgba(0, 0, 0, 0.1);
}

.checkout-section h2 {
  font-family: "Playfair Display", serif;
  font-size: 2em;
  margin-bottom: 2vw;
}

.checkout-section .form-group {
  margin-bottom: 1.5vw;
}

.checkout-section .form-group label {
  display: block;
  margin-bottom: 0.5vw;
  font-weight: bold;
}

.checkout-section .form-group input,
.checkout-section .form-group select {
  width: 95%;
  padding: 1vw;
  border-radius: 0.5vw;
  border: 0.1vw solid #ccc;
}

.checkout-section .form-group input[type="checkbox"] {
  width: auto;
  margin-right: 1vw;
}

.checkout-section .form-group .checkbox-label {
  display: inline-block;
  margin-left: 1vw;
}

.checkout-section .additional-info {
  display: none;
  margin-top: 1.5vw;
}

.checkout-section .additional-info label {
  display: block;
  margin-bottom: 0.5vw;
  font-weight: bold;
}

.checkout-section .checkout-btn {
  background-color: #32a1cd;
  color: #fff;
  border: none;
  padding: 1vw 2vw;
  font-size: 1.2em;
  font-weight: bold;
  border-radius: 0.5vw;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
  display: block;
  width: 100%;
  text-align: center;
}

.checkout-section .checkout-btn:hover {
  background-color: #228B22;
  transform: translateY(-0.2vw);
}

main {
  padding: 20px;
}

h1, h2 {
  color: darkblue;
}

form {
  margin-top: 20px;
}

label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

input[type="text"], input[type="email"], select {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button[type="submit"] {
  background-color: darkblue;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: black;
}


#cartItems .cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

#cartItems .cart-item img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

#cartItems .cart-item span {
  margin-right: 10px;
}

#cartTotal {
  font-weight: bold;
  font-size: 18px;
}

footer {
  background: hsla(213, 77%, 14%, 1);
  background: linear-gradient(90deg, hsla(213, 77%, 14%, 1) 0%, hsla(202, 27%, 45%, 1) 100%);
  color: #fff;
  padding: .5vw;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  position: relative;
  transition: background-color 0.3s ease;
}

.footer-section {
  margin: 2vw;
  opacity: 0;
  transform: translateY(2vw);
  transition: opacity 0.5s, transform 0.5s;
}

footer:hover .footer-section {
  opacity: 1;
  transform: translateY(0);
}

.footer-section h3 {
  font-size: 1.5vw;
  margin-bottom: 1vw;
}

.footer-section p, .footer-section ul {
  font-size: 1.2vw;
  margin: 0;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin: 0.5vw 0;
}

.footer-section ul li a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-section ul li a:hover {
  color: #ccc;
}

footer p {
  width: 100%;
  text-align: center;
  opacity: 0;
  transform: translateY(2vw); 
  transition: opacity 0.5s ease, transform 0.5s ease;
}

footer:hover p {
  opacity: 1; 
  transform: translateY(0); 
}

@media (max-width: 768px) {
  .checkout-section .item {
      flex-direction: column;
      align-items: flex-start;
  }

  .checkout-section .item img {
      margin-bottom: 1vw;
  }
}

/* New CSS for Cart Summary */
#cartSummary {
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
}

#cartItems .cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

#cartItems .cart-item img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

#cartItems .cart-item span {
  margin-right: 10px;
}

#cartTotal {
  font-weight: bold;
  font-size: 18px;
}