.checkout-section {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  .cart-items .cart-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
  }
  
  .cart-item-image {
    width: 60px;
    height: 60px;
    margin-right: 15px;
  }
  
  .cart-item-details {
    display: flex;
    flex-direction: column;
  }
  
  .cart-total {
    font-size: 18px;
    font-weight: bold;
    text-align: right;
  }
  
  .payment-method {
    margin: 15px 0;
  }
  
  .checkout-button {
    background-color: #007bff;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
  }
  
  .checkout-button:hover {
    background-color: #0056b3;
  }
  