header {
    margin-top: 1vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 8vw;
    position: relative;
    z-index: 100;
}

header h2{
    color: whitesmoke;
}

.logo {
    display: flex;
    align-items: center;
    color: white;
    font-size: 1.5vw;
    font-weight: bold;
}

.logo img {
    height: 12vw;
    margin-right: 2vw;
    margin-left: 2vw;
    border-radius: 50%;
    position: relative;
    transition: transform 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.logo img:hover {
    transform: scale3d(0.9, 0.9, 0.9);
}